/* =Reset
-------------------------------------------------------------- */
@import url(http://fonts.googleapis.com/css?family=Oxygen);
@import url(http://fonts.googleapis.com/css?family=Roboto+Condensed);

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}
body {
	line-height: 1;
	font-family: 'Oxygen', sans-serif;
}
.bella {font-family: 'Roboto Condensed', sans-serif;}
ol,
ul {
	list-style: none;
}
/*.entry-content blockquote {
	position: relative;	
	padding: 30px 24px 24px 24px;
	font-size: 20px;
	
	z-index:1;
	
}
.entry-content blockquote p {
	background-color: #409544;
	padding: 30px;
	color: #fff;
	border-radius: 0px 20px 0px 20px;
}*/
.entry-content blockquote {
	position: relative;	
	padding: 30px 24px 24px 24px;
	font-size: 20px;
	
}
.entry-content blockquote p {
	color: #409544;
	font-weight: bold;
	
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	background-image: url(images/quotes.png);
	background-repeat: no-repeat;
	width: 40px;
	 height: 40px;
	 text-indent:-9000px;
	  display: block;
    content: "\201C";
    position: absolute;
	z-index:1000

}
/*blockquote:before {
	background-position: -0px -80px;
    left: 25px;
    top: 27px;
}
blockquote:after {
	background-position: -0px -120px;
	 right: 25px;
    bottom: 22px;
}*/
blockquote:before {
	background-position: -0px -0px;
    left: -0px;
    top: -10px;
}
blockquote:after{
	background-position: -0px -40px;
	 right: -0px;
    bottom: -10px;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
caption,
th,
td {
	font-weight: normal;
	text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}
html {
	overflow-y: scroll;
	font-size: 100%;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}
a:focus {
	outline: thin dotted;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section {
	display: block;
}
audio,
canvas,
video {
	display: inline-block;
}
audio:not([controls]) {
	display: none;
}
del {
	color: #333;
}
ins {
	background: #fff9c0;
	text-decoration: none;
}
hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin: 24px;
}
sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sup {
	top: -0.5em;
}
sub {
	bottom: -0.25em;
}
small {
	font-size: smaller;
}
img {
	border: 0;
	-ms-interpolation-mode: bicubic;
}

/* Clearing floats */
.clear:after,
.wrapper:after,
.format-status .entry-header:after {
	clear: both;
}
.clear:before,
.clear:after,
.wrapper:before,
.wrapper:after,
.format-status .entry-header:before,
.format-status .entry-header:after {
	display: table;
	content: "";
}


/* = Repeatable patterns
-------------------------------------------------------------- */

/* Small headers */
.archive-title,
.page-title,
.widget-title,
.entry-content th,
.comment-content th {
	font-size: 11px;
	line-height: 2.181818182;
	font-weight: bold;
	text-transform: uppercase;
	color: #636363;
}

/* Shared Post Format styling */
article.format-quote footer.entry-meta,
article.format-link footer.entry-meta,
article.format-status footer.entry-meta {
	font-size: 11px;
	line-height: 2.181818182;
}

/* Form fields, general styles first */
button,
input,
textarea {
	border: 1px solid #ccc;
	border-radius: 3px;
	font-family: inherit;
	padding: 6px;
}
button,
input {
	line-height: normal;
}
textarea {
	font-size: 100%;
	overflow: auto;
	vertical-align: top;
}

/* Reset non-text input types */
input[type="checkbox"],
input[type="radio"],
input[type="file"],
input[type="hidden"],
input[type="image"],
input[type="color"] {
	border: 0;
	border-radius: 0;
	padding: 0;
}

/* Buttons */
.menu-toggle,
input[type="submit"],
input[type="button"],
input[type="reset"],
article.post-password-required input[type=submit],
.bypostauthor cite span {
	padding: 6px 10px;
	font-size: 11px;
	line-height: 1.428571429;
	font-weight: normal;
	color: #7c7c7c;
	background-color: #e6e6e6;
	background-repeat: repeat-x;
	background-image: -moz-linear-gradient(top, #f4f4f4, #e6e6e6);
	background-image: -ms-linear-gradient(top, #f4f4f4, #e6e6e6);
	background-image: -webkit-linear-gradient(top, #f4f4f4, #e6e6e6);
	background-image: -o-linear-gradient(top, #f4f4f4, #e6e6e6);
	background-image: linear-gradient(top, #f4f4f4, #e6e6e6);
	border: 1px solid #d2d2d2;
	border-radius: 3px;
	box-shadow: 0 1px 2px rgba(64, 64, 64, 0.1);
}
.menu-toggle,
button,
input[type="submit"],
input[type="button"],
input[type="reset"] {
	cursor: pointer;
}
button[disabled],
input[disabled] {
	cursor: default;
}
.menu-toggle:hover,
button:hover,
input[type="submit"]:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
article.post-password-required input[type=submit]:hover {
	color: #5e5e5e;
	background-color: #ebebeb;
	background-repeat: repeat-x;
	background-image: -moz-linear-gradient(top, #f9f9f9, #ebebeb);
	background-image: -ms-linear-gradient(top, #f9f9f9, #ebebeb);
	background-image: -webkit-linear-gradient(top, #f9f9f9, #ebebeb);
	background-image: -o-linear-gradient(top, #f9f9f9, #ebebeb);
	background-image: linear-gradient(top, #f9f9f9, #ebebeb);
}
.menu-toggle:active,
.menu-toggle.toggled-on,
button:active,
input[type="submit"]:active,
input[type="button"]:active,
input[type="reset"]:active {
	color: #757575;
	background-color: #e1e1e1;
	background-repeat: repeat-x;
	background-image: -moz-linear-gradient(top, #ebebeb, #e1e1e1);
	background-image: -ms-linear-gradient(top, #ebebeb, #e1e1e1);
	background-image: -webkit-linear-gradient(top, #ebebeb, #e1e1e1);
	background-image: -o-linear-gradient(top, #ebebeb, #e1e1e1);
	background-image: linear-gradient(top, #ebebeb, #e1e1e1);
	box-shadow: inset 0 0 8px 2px #c6c6c6, 0 1px 0 0 #f4f4f4;
	border-color: transparent;
}
.bypostauthor cite span {
	color: #fff;
	background-color: #21759b;
	background-image: none;
	border: 1px solid #1f6f93;
	border-radius: 2px;
	box-shadow: none;
	padding: 0;
}

/* Responsive images */
.entry-content img,
.comment-content img,
.widget img {
	max-width: 100%; /* Fluid images for posts, comments, and widgets */
}
img[class*="align"],
img[class*="wp-image-"],
img[class*="attachment-"] {
	height: auto; /* Make sure images with WordPress-added height and width attributes are scaled correctly */
}
img.size-full,
img.size-large,
img.header-image,
img.wp-post-image {
	max-width: 100%;
	height: auto; /* Make sure images with WordPress-added height and width attributes are scaled correctly */
}

/* Make sure videos and embeds fit their containers */
embed,
iframe,
object,
video {
	max-width: 100%;
}
.entry-content .twitter-tweet-rendered {
	max-width: 100% !important; /* Override the Twitter embed fixed width */
}

/* Images */
.alignleft {
	float: left;
}
.alignright {
	float: right;
}
.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
}


/* Navigation */
.site-content nav {
	clear: both;
	line-height: 2;
	overflow: hidden;
}
#nav-above {
	padding: 24px 0;
}
#nav-above {
	display: none;
}
.paged #nav-above {
	display: block;
}
.nav-previous,
.previous-image {
	float: left;
	width: 50%;
}
.nav-next,
.next-image {
	float: right;
	text-align: right;
	width: 50%;
}
.nav-single + .comments-area,
#comment-nav-above {
	margin: 48px 0;
}


/* =Basic structure
-------------------------------------------------------------- */

/* Body, links, basics */
html {
	font-size: 87.5%;
}
body {
	font-size: 14px;
	text-rendering: optimizeLegibility;
	color: #444;
}
a {
	outline: none;
	color: #21759b;
}
a:hover {
	color: #0f3647;
}

/* Assistive text */
.assistive-text,
.site .screen-reader-text {
	position: absolute !important;
	clip: rect(1px, 1px, 1px, 1px);
}
.main-navigation .assistive-text:focus {
	background: #fff;
	border: 2px solid #333;
	border-radius: 3px;
	clip: auto !important;
	color: #000;
	display: block;
	font-size: 12px;
	padding: 12px;
	position: absolute;
	top: 5px;
	left: 5px;
	z-index: 100000; /* Above WP toolbar */
}

/* Page structure */
.site {
	padding: 0 24px;
	background-color: #fff;
}
.site-content {
	margin: 24px 0 0;
}
.widget-area, .widget-area-home {
	margin: 24px 0 0;
}
.top-header {
	width: 100%;
	padding: 0 0 25px 0;
	background-color: #fff;
	-webkit-box-shadow: 0px 3px 5px -1px rgba(133,129,133,1);
	-moz-box-shadow: 0px 3px 5px -1px rgba(133,129,133,1);
	box-shadow: 0px 3px 5px -1px rgba(133,129,133,1);
	position: relative;
	z-index:1000;	
	margin: 0 0 10px 0;
}
.address-top {
	width: 100%;
	margin: 25px auto 0 auto;	
	text-align: center;
	color:#133971;
}
.header-right {
	display: inline-block;
	width: 100%;
	float:none;
	margin: 10px auto;
	clear: none;
	text-align: center;
}
/* Social */
#social {
	width: 40px;
	position: relative;
	float: left;
	margin: 0px ;
	z-index: 6;	
}

#social ul {
	margin: 0px;
	padding: 0px;
	list-style-type: none;
	z-index: 2;
	position: relative;
}
#social ul li {
	float: left;
	position: relative;
	margin: 0px 5px;
}
#social ul a, .phone-top-icon {
	text-decoration: none;
	background-repeat: no-repeat;
	display: inline-block;
	height: 38px;
	text-indent: -9000px;
	position: relative;
	background-image: url(images/icons.png);
	background-size: 300px 38px;
}
#social .linkedin a {
	width:38px;
	background-position: -0px -0px;
	position: relative;
}
#social .linkedin a:hover {
	background-position: -0px -0px;
}
.phone-top-icon {
	background-position: -45px 0px;
	background-repeat: no-repeat;
	width: 38px;
	height: 38px;
}
.phone-top {
	text-decoration: none;
	display: inline-block;
	height: 38px;
	position: relative;
	padding: 0 0 0 0px;
	text-align: right;
	font-size: 24px;
	color: #133971;
	width: auto;
	float:none;
	margin: 0px auto;
}
.phone {
	width: auto;
	float: right;
	display: inline-block;
	margin: 8px 0 0 5px;	
}
/* Logo */
.logo , h1.logo {
	width: 250px;
	height: 64px;
	background-image: url(images/logo.png);	
	z-index: 1;
	margin: 20px auto;
	background-size: cover;
}
.logo a, h1.logo a{
	width: 250px;
	height: 64px;
	display: block;
	text-indent: -9000px;
}


/* = Navigation
-------------------------------------------------------------- */
.main-navigation {
	margin-top: 24px;
	text-align: center;
}
.main-navigation li {
	margin-top: 24px;
	font-size: 12px;
	line-height: 1.42857143;
}
.main-navigation a {
	color: #5e5e5e;
}
.main-navigation a:hover {
	color: #21759b;
}
.main-navigation ul.nav-menu,
.main-navigation div.nav-menu > ul {
	display: none;
}
.main-navigation ul.nav-menu.toggled-on,
.menu-toggle {
	display: inline-block;
}
.menu-toggle {
	margin: 0 0 40px 0;
}
.backtotop {
	font-size: 12px;
	margin: 10px 0 0 0;	
}
.backtotop a {
	width: auto;
	display: inline-block;
	background-color:rgba(90,136,196,.7);
	border-radius: 3px;
	padding: 3px;
	color: #fff;
	text-decoration:none;
}
.backtotop a:hover {
	background-color:rgba(90,136,196,1);
}
a.anchor{display: block; position: relative; top: -250px; visibility: hidden;}
a.anchor-person {display: block; position: relative; top: -50px; visibility: hidden;}
/* Sidebar */
.widget_recent_entries li {
	margin: 0 0 20px 0;	
}

.widget_recent_entries li a {
	text-decoration: none;	
}
.widget-area .widget {
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto;
	margin-bottom: 48px;
	word-wrap: break-word;
}
.widget-area .widget h3 {
	margin-bottom: 24px;
}
.widget-area .widget p,
.widget-area .widget li,
.widget-area .widget .textwidget {
	font-size: 13px;
	line-height: 1.846153846;
}
.widget-area .widget p {
	margin-bottom: 24px;
}
.widget-area .textwidget ul {
	list-style: disc outside;
	margin: 0 0 24px;
}
.widget-area .textwidget li {
	margin-left: 36px;
}
.widget-area .widget a {
	color: #757575;
}
.widget-area .widget a:hover {
	color: #21759b;
}
.widget-area .widget a:visited {
	color: #9f9f9f;
}
.widget-area #s {
	width: 53.66666666666%; /* define a width to avoid dropping a wider submit button */
}

/* Footer */
footer[role="contentinfo"] {
	clear: both;
	font-size: 12px;
	line-height: 2;
	margin-top: 24px;
	margin-left: auto;
	margin-right: auto;
	padding: 24px 10px;
	color: #fff;
	background-color: #5a88c4;
}
footer[role="contentinfo"] a {
	color: #fff;
	text-decoration: none;
}
footer[role="contentinfo"] a:hover {
	color: #fff;
}
.footer-inside {
	width: 100%;
	position: relative;
	text-align: center;
	padding: 0 0 20px 0;
}
.footer-address {
	width: 100%;
	display: inline-block;
	float:none;	
	text-align: center;
	
}
.footer-credits {
	width: 100%;
	display: inline-block;
	float:none;	
	text-align: center;
}
/* = Pages and Page Templates
-------------------------------------------------------------- */
#content-area {
	width: 100%;
	position: relative;	
}
#content-left {
	width: 100%;
	margin: 0 0 20px 0;
	position: relative;	
}
#content-right {
	width: 100%;
	margin: 0 0 20px 0;
	position: relative;	
}
#content {
	padding: 0 0 10px 0;	
}
section.manufactor-list {
	width: 100%;
	padding: 40px 0;
	ul {
	  columns: 2;
	  -webkit-columns: 2;
	  -moz-columns: 2;
	  list-style: none;
	  margin: 0;
	  padding: 0;
	}
	li {
		margin: 0;
		&:hover {
			svg {
				padding: 0 0 0 5px;
			}
		}
	}
	a {
		text-decoration: none;
	}
}
/* = Home page
-------------------------------------------------------------- */
