}
.entry-content .state-areas ul li {
	font-size: 18px;
	list-style: none;
	margin: 0 0 5px 0;
	line-height: 1;
}
.man-website {
	margin: 0 0 20px 0;	
}
/* =Main content and comment content
-------------------------------------------------------------- */
.blue-arrow {
	width: 57px;
	height:57px;
	position: relative;
}
.blue-arrow a {
	width: 57px;
	height:57px;
	display: block;
	background-image: url(images/arrows.png);
	background-repeat: no-repeat;
	background-position: -0px 0px;
	background-color: #133971;
	text-indent: -9000px;	
	border-radius: 10px;
}
.blue-arrow a:hover {
	background-position: 3px 0px;
	background-color: #5a88c4;
}
/* =News
-------------------------------------------------------------- */
.post {
	border-bottom: 4px solid #eee;
	padding: 10px 0 10px 0;
	margin: 0 0 20px 0;	
}
.post a {
	text-decoration : none;	
}
.post h2 a {
	text-decoration: none;
}
.home-post {
	margin: 0 0 40px 0;	
	position:  relative;
	padding: 5px;
}
.homereadmore, .homereadmore a {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
}
.homereadmore a {
	display: block;
	text-decoration:  none;
	background-color: rbga(255,255,255,0);
	transition: background-color 0.3s ease;
	text-indent: -9000px;
}
.home-post-date {
	font-size: 20px;	
}
/* =Markets
-------------------------------------------------------------- */
ul.product-list li, ul.product-list-full li {
	width: auto;
	float: left;
	margin: 2px;
