.team-box {
	background-color: #5887C6;
	border-radius: 10px;
	padding: 10px;
	color:#fff;
	margin: 0 5px 5px 0;
	width: 180px; 
	height: 150px;
	float:left;
	line-height: 1.3;
	font-size: 13px;
}
.listareas {
	font-size: 11px;	
}
.listareas a {
	text-decoration: none;
	color: #000;
}
.team-box-right {
	width: 100px;
	float:right;
	overflow: hidden;	
	position: relative;
	height: 150px;
}
img.teamleft {
	margin: 0 10px 0px 0;	
	border-radius: 10px 0 0 0;
}
.team-contact {
	width: auto;
	position: absolute;
	bottom: 0px;
	right: 0px;	
}
.team-contact a {
	width: auto;
	display: inline-block;	
	background-color: #fff;
	color: #999;
	text-align:center;
	text-decoration: none;
	padding: 5px 10px ;
	border-radius: 5px;
}
.team-box h3 {
	/*width: auto;
	display: inline-block;*/
	margin: 0px 0 2px 0;	
}
.state-marker {
	position: absolute;
	
}
.state-marker a {
	width: 40px;
	height:30px;
	background-image: url(images/bubbles.png);	
	background-position: 0px 0px;
	display: block;
	padding: 10px 0px 10px 30px;
	color: #fff;
	text-decoration: none;	
}
.state-marker a:hover, .state-marker a.active  {
	background-position: -0px -50px;	
}
.de {top: 40px; right: -5px;}
.md {top: 40px; right:50px;}
.dc {top: 88px; right: 28px;}
.nc {top: 230px; right: 70px;}
.sc {top: 310px; right: 140px;}
.va {top: 150px; right: 80px;}
/* Footer
------------------------------------- */
.footer-inside {
	width: 1120px;
	margin: 10px auto;	
}

}


/* =Print
----------------------------------------------- */

@media print {
	body {
		background: none !important;
		color: #000;
		font-size: 10pt;
	}
	footer a[rel=bookmark]:link:after,
	footer a[rel=bookmark]:visited:after {
		content: " [" attr(href) "] "; /* Show URLs */
	}
	a {
		text-decoration: none;
	}
	.entry-content img,
	.comment-content img,
	.author-avatar img,
	img.wp-post-image {
		border-radius: 0;
		box-shadow: none;
	}
	.site {
		clear: both !important;
		display: block !important;
		float: none !important;
		max-width: 100%;
		position: relative !important;
	}
	.site-header {
		margin-bottom: 72px;
		text-align: left;
	}
	.site-header h1 {
		font-size: 21pt;
		line-height: 1;
		text-align: left;
	}
	.site-header h2 {
		color: #000;
		font-size: 10pt;
		text-align: left;
	}
	.site-header h1 a,
	.site-header h2 a {
		color: #000;
	}
	.author-avatar,
	#colophon,
	#respond,
	.commentlist .comment-edit-link,
	.commentlist .reply,
	.entry-header .comments-link,
	.entry-meta .edit-link a,
	.page-link,
	.site-content nav,
	.widget-area,
	img.header-image,
	.main-navigation {
		display: none;
	}
	.wrapper {
		border-top: none;
		box-shadow: none;
	}
	.site-content {
		margin: 0;
		width: auto;
	}
	.singular .entry-header .entry-meta {
		position: static;
	}
	.singular .site-content,
	.singular .entry-header,
	.singular .entry-content,
	.singular footer.entry-meta,
	.singular .comments-title {
		margin: 0;
		width: 100%;
	}
	.entry-header .entry-title,
	.entry-title,
	.singular .entry-title {
		font-size: 21pt;
	}
	footer.entry-meta,
	footer.entry-meta a {
		color: #444;
		font-size: 10pt;
	}
	.author-description {
		float: none;
		width: auto;
	}

	/* Comments */
	.commentlist > li.comment {
		background: none;
		position: relative;
		width: auto;
	}
	.commentlist .avatar {
		height: 39px;
		left: 2.2em;
		top: 2.2em;
		width: 39px;
	}
	.comments-area article header cite,
	.comments-area article header time {
		margin-left: 50px;
	}
}